<script>
  import Barcode from "svelte-barcode";
  let prefix = "";
  let start = 1;
  let stop = 10;
  let showbarcodes = false;
  let barcodes = [];
  let barcode_options = {
    format: "CODE128",
    width: 2,
    height: 100,
    text: "",
    textAlign: "center",
    textPosition: "bottom",
    textMargin: 2,
    fontSize: 20,
    background: "#ffffff",
    lineColor: "#000000"
  };

  function pad(num, size) {
    num = num.toString();
    while (num.length < size) num = "0" + num;
    return num;
  }

  function generate() {
    showbarcodes = false;
    for (let i = start; i < stop; ++i) {
      barcodes.push(prefix + pad(i, 4));
    }
    showbarcodes = true;
  }
</script>

<style>
  main {
    font-family: sans-serif;
    text-align: center;
  }
</style>

<main>
	<h1>Barcodes</h1>
  <div>
	<input type="text" bind:value={prefix} placeholder="Prefix"/>
  <input type="number" bind:value={start} placeholder="Start Number"/>
  <input type="number" bind:value={stop} placeholder="Stop Number"/>
	<button type="button" on:click={generate}>Generate</button>
  </div>
  {#if showbarcodes}
  <div>
    {#each barcodes as barcode,i}
      <Barcode
      value={barcode}
      elementTag={'canvas'+i}
      options={barcode_options}
    />
    {/each}
  </div>
  {/if}
</main>